/* ************************
   03.4: Footer
   ********************* */

   .footer {
      background: transparent radial-gradient(closest-side at 50% 50%, #7875F4 0%, #5551EF 100%) 0% 0% no-repeat;
      position: relative;
      z-index: 1;
      @extend .text-white;

      .widget {
         background-color: transparent;
         margin-bottom: 60px;

         .widget-title {
            margin-bottom: 34px;
            position: relative;
            padding-left: 14px;
            &:after {
                width: 3px;
                height: 17px;
                @extend .white-bg;
                left: 0;
                top: 3.5px;
                content: "";
                position: absolute;
            }
         }
         &.widget_contact {
            ul {
                li {
                    a {
                        @extend .white;
                        &:hover {
                            @extend .c2;
                        }
                    }
                }
            }
        }
        &.widget_recent_entries {
            ul {
               li {
                  .posted-on {
                        i, a {
                           @extend .white;
                        }
                  }
                  .post-title {
                        a {
                           @extend .white;
                           &:hover {
                              @extend .white;
                           }
                        }
                  }
               }
            }
         }
      }
   }

   .footer-bg-shape {
      background-repeat: no-repeat;
      background-position: center center;
      background-image: url(../img/media/footer-bg.png);
      background-size: cover;
      position: absolute;
      height: 100%;
      width: 100%;
      z-index: -1;
   }

   .footer-top {
      padding: 80px 0 0px;
   }

   .footer-bottom-text {
      position: relative;
      padding: 30px 0;
      &:after {
         width: 100%;
         height: 1px;
         left: 0;
         top: 0;
         @extend .white-bg;
         position: absolute;
         content: "";
         opacity: 0.15;
      }
   }


   .footer {
      &.style--two {
         background-color: transparent;
         background-image: linear-gradient(255deg, #01064A 0%, #00D7FF 100%);
      }
      &.style--three {
         background-color: transparent;
         background-image: linear-gradient(255deg, #28345F 0%, #030F38 100%);
      }
   }