/* ************************
   02.1: Common Classes
   ********************* */
/* Transitions */
.trans2 {
    @include transition(.2s, ease-in-out);
}

.trans3 {
    @include transition(.3s, ease-in-out);
}

.trans4 {
    @include transition(.4s, ease-in-out);
}

.trans5 {
    @include transition(.5s, ease-in-out);
}

.trans7 {
    @include transition(.5s, ease-in-out);
}

/* Font Family */
h1,
h2,
h3,
h4,
h5,
h6,
.nunito {
    font-family: $nunito;
}

body,
.rubik {
    font-family: $rubik;
}

/* Text White */
.text-white {
    *,
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        color: $white;
    }
} 

/* Border Radius */
.radius-5 {
    border-radius: 5px;
}

.radius-10 {
    border-radius: 10px;
}

.radius-50 {
    border-radius: 50px;
}

.radius {
    border-radius: 50%;
}

.regular {
    font-weight: $regular;
}

.medium {
    font-weight: $medium;
}

.semi-bold {
    font-weight: $semi-bold;
}

.bold {
    font-weight: $bold;
}

.extra-bold {
    font-weight: $extra-bold;
}

/* Box Shadow */
.box-shadow {
    box-shadow: 0 5px 40px rgba(0, 0, 0, 0.05);
}

.box-shadow2 {
    box-shadow: 10px 10px 60px rgba(0, 0, 0, 0.07);
}

.no-shadow {
    box-shadow: none !important;
}

.bg-img {
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 90%;
    &.service {
        background-position: center right;
    }
}

.section-bg {
    background-color: rgba($c1, 0.03);
}

.gradient-bg {
    background-color: transparent;
    background-image: radial-gradient(at center center, #7875F4 0%, #5551EF 100%);
}


.gradient-bg2 {
    background-color: transparent;
    background-image: radial-gradient(at center center, #34F093 0%, #0CE177 100%);
}

.gradient-section-bg {
    background-color: transparent;
    background-image: linear-gradient(67deg, #00D7FF -30%, #01064A 100%);
}

.section-pattern-img {
    position: absolute;
    width: 90%;
    left: 5%;
}

.app-section {
    padding-top: 40px;
    padding-bottom: 20px;
    @include tab {
        padding-top: 60px;
        padding-bottom: 60px;

        .app-img {
            margin-bottom: 40px;
        }
    }
}

// Hover Effect
.hover-effect {
    @extend .trans7;
    @extend .ov-hidden;
    position: relative;
    z-index: 1;
    &:after {
        width: 150%;
        height: 150%;
        content: "";
        @extend .c1-bg;
        position: absolute;
        right: -25%;
        bottom: -25%;
        z-index: -1;
        @extend .radius;
        @extend .trans5;
        transform: scale(0.1);
        transform-origin: 100% 100%;
    }

    &:hover,
    &.active {
        @extend .c1-bg;
        &:after {
            transform: scale(1);
        }
    }
    &.style--three {
        &:after {
            background-color: transparent;
            background-image: radial-gradient(at center center, #34F093 0%, #0CE177 100%);
        }
        &:hover,
        &.active {
            background-color: #34F093;
            &:after {
                transform: scale(1);
            }
        }
    }
}

/* List Style */
.list-unstyled {
    padding: 0;
    margin: 0;
    list-style-type: none;
}

.list-inline {
    @extend .list-unstyled;
    li {
        display: inline-block;
    }
}

.list-check {
    margin-bottom: 0;
    padding: 0;
    list-style: none;
    li {
        font-size: 18px;
        line-height: 1.67;
        padding-left: 35px;
        position: relative;
        &:not(:last-child) {
            margin-bottom: 20px;
        }
        &:after {
            position: absolute;
            @include fa('\f00c');
            line-height: inherit;
            @extend .c2;
            left: 5px;
            top: -2px;
        }

        &:before {
            position: absolute;
            left: 0;
            top: 5px;
            content: '';
            width: 18px;
            height: 18px;
            border-radius: 50%;
            background-color: #FF988D;
            z-index: -1;
            opacity: .2;
        }
    }
}

.service_point-list {
    @extend .list-unstyled;
    display: flex;
    flex-wrap: wrap;
    li {
        flex: 0 0 50%;
        font-size: 18px;
        @extend .black;
        margin-bottom: 20px;
        padding-left: 15px;
        position: relative;
        i {
            position: absolute;
            left: 0;
            top: 5px;
        }
    }
}

.contact-info-list {
    .single-contact {
        align-items: center;
        &:not(:last-child) {
            margin-bottom: 30px;
        }

        .contact-icon {
            width: 60px;
            min-width: 60px;
            height: 60px;
            background-color: rgba($c1, 0.1);
            @extend .radius;
            margin-right: 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            @extend .c1;
        }
        .contact-content {
            a {
                display: block;
                @extend .text-color;
                &:hover {
                    @extend .c1;
                }
            }
        }
    }
}

.service-list {
    @extend .list-unstyled;
    li {
        position: relative;
        padding-left: 22px;
        &:not(:last-child) {
            margin-bottom: 15px;
        }
        &:after {
            position: absolute;
            background-image: url(../img/icons/right-arrow.svg);
            content: "";
            left: 0;
            top: 8px;
            width: 12px;
            height: 12px;
            background-repeat: no-repeat;
        }
    }
}

#countdown {
    @extend .list-unstyled;
    li {
        width: 100px;
        height: 100px;
        @extend .white-bg;
        border-radius: 3px;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 30px;
        &:not(:last-child) {
           margin-right: 26px;
        }
     }

     .single-countdown {
        display: inline-block;
        text-align: center;
        h2 {
           @extend .c1;
           @extend .extra-bold;
           font-size: 42px;

        }
        span {
           font-size: 12px;
           text-transform: uppercase;
           @extend .black;
        }
     }
}

// Social Links
.social-links {
    a {
        width: 40px;
        height: 40px;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        background-color: rgba($c1, 0.1);
        @extend .radius;

        &:not(:last-child) {
            margin-right: 7px;
        }

        &:hover {
            @extend .white;
            @extend .c2-bg;
        }

    }
    &.style--two {
        a {
            background-color: rgba($white, 0.1);
            &:not(:last-child) {
                margin-right: 10px;
            }
            &:hover {
                @extend .white;
                @extend .c2-bg;
            }
        }
    }
}

.logo-carousel {
    padding: 60px 0 50px;
    &.style--two {
        padding: 120px 0;
    }
}
.testimonial-carousel {
    padding-top: 24px;
}

// Video Btn
.video-btn {
	font-size: 16px;
	text-transform: uppercase;
	font-family: "Nunito Sans", sans-serif;
	line-height: 1;
	color: #fff;
	font-weight: 700;
	display: inline-flex;
    align-items: center;
    i {
        width: 65px;
        height: 65px;
        border-radius: 50%;
        background-color: #0FE705;
        color: #fff;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        margin-right: 16px;
        transition: .3s;
    }

    &:hover {
        color: #fff;
        i {
            color: #5551EF;
        }
    }

    &.style--two {
        position: relative;
        z-index: 1;
        i {
            width: 80px;
            height: 80px;
            margin: 0;
            @extend .c1-bg;
        }
        &:after {
            width: 100%;
            height: 100%;
            background-color: $white;
            position: absolute;
            left: 0;
            top: 0;
            content: "";
            transform: scale(1);
            @extend .radius;
            opacity: 0;
            animation: ripple 1.4s ease-in 0s infinite forwards;
            z-index: -1;
        }
        &:hover {
            i {
                @extend .c2;
            }
        }
    }
}

/* Owl Dots */
.owl-carousel {
    .owl-dots {
        left: 50%;
        transform: translateX(-50%);
        position: absolute;
        display: flex;
        bottom: 0;
    }
    button.owl-dot {
        margin: 0 7.5px;
        width: 7px;
        height: 7px;
        display: block;
        border-radius: 50%;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        position: relative;
        background-color: rgba(85, 81, 239, 0.2);
        &.active {
            background-color: rgba(85, 81, 239, 1);
            width: 9px;
            height: 9px;
            &:after {
                content: "";
                position: absolute;
                background-color: rgba(85, 81, 239, 0.2);
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                border-radius: 50%;
                transform: scale(2);
            }
        }
    }

    &.style--two {
        button.owl-dot {
            background-color: rgba(0, 215, 255, 0.3);
            &.active {
                background-color: rgba(0, 215, 255, 1);
                &:after {
                    background-color: rgba(0, 215, 255, 0.15);
                }
            }
        } 
    }
    &.style--three {
        button.owl-dot {
            background-color: rgba($c2, 0.3);
            &.active {
                background-color: rgba($c2, 1);
                &:after {
                    background-color: rgba($c2, 0.15);
                }
            }
        } 
    }
}

/* Theme Input Group */
.theme-input-group {
    position: relative;
    width: 100%;
    input {
        width: 100%;
        height: 50px;
        border: none;
        padding-left: 15px;
        padding-right: 50px;
        color: $white;
        background-color: #334E95;
    }
    button {
        position: absolute; 
        top: 4px;
        right: 4px;
        height: calc(100% - 8px);
        padding: 9px 28px;
        line-height: 1;
        font-weight: $semi-bold;
        color: $white;
        @extend .trans2;
        @extend .c2-bg;
        &:hover {
            background-color: #fb9389;
        }
    }
}

/* Theme Input Style */
.form-control {
    border: none;
    border-bottom: 1px solid rgba(0, 0, 0, 0.09);
    background-color: transparent;
    height: 52px;
    width: 100%;
    padding: 0 20px;
    font-size: 16px;
    @extend .trans3;
    margin-bottom: 30px;
    border-radius: 0;
    &:focus {
        background-color: rgba(0, 0, 0, 0.01);;
        box-shadow: none;
    }
}

textarea.form-control {
    height: 170px;
    padding: 20px 20px;
}

/* Others */
.ov-hidden {
    overflow: hidden;
}
.ovx-hidden {
    overflow-x: hidden;
}

/* Pagination */
.pagination {
    li {
        &:not(:last-child) {
            margin-right: 5px;
        }
        a,
        span {
            width: 50px;
            height: 50px;
            line-height: 1;
            position: relative;
            display: inline-flex;
            justify-content: center;
            align-items: center;
            background-color: rgba($c1, .1);
            @extend .c1;
            @extend .nunito;
            @extend .bold;
            @extend .radius;
            &.current {
                background-color: $c2;
                @extend .white;
            }
            svg path {
                @extend .trans3;
            }
            &:hover,
            &.active {
                color: $white;
                @extend .c2-bg;

                svg path {
                    stroke: #fff;
                    fill: #fff;
                }
            }
        }
        &.nav-btn {
            a.disabled {
                cursor: not-allowed;
                opacity: .5;
            }
        }
    }
}

/* Blockquote */
blockquote {
    max-width: 570px;
    margin: 0 auto 30px;
    @extend .nunito;
    P {
        font-size: 18px;
        line-height: 1.67;
        font-style: italic;
        text-decoration: underline;
        margin-bottom: 15px !important;
    }
    cite {
        font-style: normal;
        font-size: 16px;
        position: relative;
        padding-left: 20px;
        line-height: 1;
        &:after {
            position: absolute;
            content: '';
            left: 0;
            top: 50%;
            transform: translateY(-50%);
            height: 3px;
            width: 10px;
            @extend .text-color-bg;
        }
    }
}

/* Form Response */
.form-response {
    background-color: rgba($c1, .2);
    span {
        display: block;
        margin-top: 20px;
        margin-bottom: 30px;
        padding: 10px 15px;
    }  
}

/* Search Form */
.search-form {
    .theme-input-group {
        border: 1px solid #5551EF;
        @extend .radius-5;
        input {
            background-color: transparent;
            @extend .text-color;
            &::placeholder {
                color: rgba(110, 110, 110, .45) !important;
            }
            &:focus {
                background-color: transparent;
            }
        }

        button {
            background-color: transparent;
            padding: 9px 25px;
        }
    }
}

/* Gutter 50 */
@media only screen and (min-width: 992px) {
	.gutter-50 {
		margin-left: -25px;
		margin-right: -25px;
	}

	.gutter-50>[class*="col-"] {
		padding-left: 25px;
		padding-right: 25px;
	}
}

section {
    @extend .ovx-hidden;
}