/* ************************
   04.5: Team
   ********************* */

   .team-carousel .owl-stage-outer {
       margin: -20px;
       padding: 20px;
   }

   .single-team {
        box-shadow: 0px 0px 15px rgba(34, 34, 34, 0.1);
        @extend .white-bg;
        @extend .radius-10;
        padding: 60px 15px;
        margin-bottom: 30px;
        @extend .hover-effect;

        .member-img {
            border: 5px solid $white;
            @extend .box-shadow;
            @extend .radius;
            // @extend .ov-hidden;
            width: 170px;
            height: 170px;
            margin: 0 auto 30px;
            position: relative;

            > img {
                @extend .radius;
            }

            .btn-rounded {
                width: 44px;
                height: 44px;
                @extend .radius;
                @extend .white-bg;
                position: absolute;
                bottom: 15px;
                right: -8px;
                display: flex;
                align-items: center;
                justify-content: center;
            }
        }

        .member-details {
            margin-bottom: 24px;
            @extend .trans3;

            h3 {
                margin-bottom: 12px;
                @extend .trans3;
            }
        }

        &.active,
        &:hover {
            .member-details {
                @extend .white;
                h3 {
                    @extend .white;
                }
            }

            .social-links {
                a {
                    background-color: rgba($white, 0.1);
                    @extend .white;

                    &:hover {
                        @extend .c2-bg;
                    }
                }
            }
        }

        &.style--three {
            .member-details {
                h3 {
                    color: #081F70;
                }
            }
            .social-links {
                a {
                    background-color: rgba(12, 225, 119, 0.09);
                    @extend .c2;

                    &:hover {
                        @extend .c2-bg;
                    }
                }
            }
            &:before {
                background-image: url(../img/media/service-box-bg.png);
                background-repeat: no-repeat;
                position: absolute;
                left: 120px;
                top: 0;
                content: "";
                width: 100%;
                height: 100%;
                z-index: -1;
                @extend .trans5;
            }
            &:after {
                z-index: -2;
            }
            &.active,
            &:hover {
                .member-details {
                    h3 {
                        @extend .white;
                    }
                }

                .social-links {
                    a {
                        background-color: rgba(8, 31, 112, 0.09);
                        @extend .white;

                        &:hover {
                            background-color: #081F70;
                        }
                    }
                }
                &:before {
                    left: 0px;
                }
            }
        }
    }