/* ************************
   04.3: Service
   ********************* */

    .single-service {
       @extend .box-shadow2;
       @extend .white-bg;
       @extend .radius-10;
       text-align: center;
       padding: 60px 45px 57px;
       margin-bottom: 30px;
       @include mobileSm {
           padding: 50px 30px;
       }

       .service-icon {
           width: 100px;
           height: 100px;
           background-color: rgba(255, 136, 41, 0.1);
           @extend .radius;
           @extend .trans5;
           margin: 0 auto 30px;
           display: flex;
           align-items: center;
           justify-content: center;
       }

       .service-content {
           h3 {
               margin-bottom: 16px;
           }
           h3, p {
               @extend .trans3;
           }
       }

       &:hover,
       &.active {
           .service-icon {
               background-color: #fff !important;
           }
           .service-content {
               @extend .white;
               h3 {
                   @extend .white;
               }
           }
           .btn-link {
               @extend .white;
               svg path {
                   stroke: $white;
               }
           }
       }

        &.style--two {
            @extend .no-shadow;
            text-align: left;
            padding-top: 0;
            .service-icon {
                margin: 0;
                margin-bottom: 30px;
                background-color: rgba(0, 215, 255, 0.1);
            }
            .btn-link {
                @extend .black;
                svg path {
                    stroke: $black;
                }
                &:hover {
                    color: #00D7FF;
                    svg path {
                        stroke: #00D7FF;
                    }
                }
            }
            &:hover,
            &.active {
                .service-icon {
                    background-color: rgba(0, 215, 255, 0.1) !important;
                }
                .service-content {
                    @extend .text-color;
                    h3 {
                        @extend .black;
                    }
                    .btn-link{
                        color: #00D7FF;
                        svg path {
                            stroke: #00D7FF;
                        }
                    }
                }
            }
        }
        &.style--three {
            @extend .no-shadow;
            .service-icon {
                background-color: rgba(12, 225, 119, 0.09);
            }

            .btn-link {
                @extend .black;
                svg path {
                    stroke: $black;
                }
            }

            &:before {
                background-image: url(../img/media/service-box-bg.png);
                position: absolute;
                left: 120px;
                top: 0;
                content: "";
                width: 100%;
                height: 100%;
                z-index: -1;
                opacity: 0;
                @extend .trans5;
            }
            &:after {
                z-index: -2;
            }

            &:hover,
            &.active {
                &:before {
                    left: 0;
                    opacity: 1;
                }
                .btn-link {
                    @extend .white;
                    svg path {
                        stroke: $white;
                    }
                }
                .service-icon {
                    background-color: rgba(255, 255, 255, 1);
                }
            }
        }

        &.two {
            .service-icon {
                background-color: #eae9fc;
            }
        }
        &.three {
            .service-icon {
                background-color: #e7fde6;
            }
        }
        &.four {
            .service-icon {
                background-color: #eff7fd;
            }
        }
        &.five {
            .service-icon {
                background-color: #fcedec;
            }
        }
        &.six {
            .service-icon {
                background-color: #eafaf7;
            }
        }
    }


    //Service Details
    .video-wrap {
        min-height: 340px;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        background-size: cover;
        border-radius: 10px;
        overflow: hidden;
        @include tab {
            margin-bottom: 50px;
        }
    }

    .service-details {
        background-size: auto;
        background-position: center right;
        h2 {
            margin-bottom: 25px;
            line-height: 1.2;
        }
        p {
            margin-bottom: 26px;
        }
    }

    .accordion {
        h3 {
            margin-bottom: 24px;
            position: relative;
            padding-left: 30px;
            cursor: pointer;
            font-weight: 800;
            @extend .trans5;
            &:after {
                position: absolute;
                left: 0;
                top: 50%;
                transform: translateY(-50%);
                content: "";
                background-image: url(../img/icons/plus.svg);
                width: 10px;
                height: 10px;
                background-repeat: no-repeat;
                @extend .trans3;
            }
        }
        .accordion-content {
            margin-bottom: 24px;
        }
        .active {
            h3:after {
                background-image: url(../img/icons/right-arrow.svg);
                width: 12px;
            }
        }
        > div {
            overflow: hidden;
            &:not(:last-child) {
                margin-bottom: 10px;
            }
        }
    }